
#docs-viewer{background:#fefefe;color:#111;padding:16px
}
#docs-viewer h1{margin:0
}
#docs-viewer hr{border:0;border-bottom:1px solid #ddd;margin:16px 0
}
#docs-viewer pre{margin:16px;padding:8px;color:#444;background:#F0F0F0
}
#docs-viewer .info,#docs-viewer .warn{display:inline-block;margin:0;padding:8px;border:1px solid;border-left:2px solid
}
#docs-viewer .info{background:#c1e2fc;border-color:#90cbf9;border-left-color:#48a8f5
}
#docs-viewer .warn{background:#ffe6e6;border-color:#ffb3b3;border-left-color:#f66
}
#docs-viewer .docs-type:not(:last-child):after{content:' or ';font-weight:normal
}
#docs-viewer>.container{display:-ms-flexbox;display:flex
}
@media (max-width: 46.24em){
#docs-viewer>.container{-ms-flex-direction:column;flex-direction:column
}
}
.docs-page{-ms-flex:1 1 auto;flex:1 1 auto;padding:16px 32px
}
@media (max-width: 46.24em){
.docs-page{padding:16px 0
}
}
.docs-page img{max-width:100%
}
#docs-meta{margin:30px 0;text-align:center;font-size:0.8em
}
#docs-meta p{display:inline;color:#aaa;transition:all 0.3s
}
#docs-meta p:hover{font-size:0.9rem;color:#373737
}
.badge{margin-left:2px;padding:5px;color:white;background:#51adf6;border-radius:3px;text-transform:uppercase;font-size:0.85rem;font-weight:bold
}
.badge:first-of-type{margin-left:8px
}
#scroll-top{position:fixed;bottom:20px;right:20px;padding:10px;border-radius:3px;background:#2196F3;color:white;cursor:pointer;transition:opacity 0.5s, background 0.3s;display:none;opacity:0
}
#scroll-top:hover{background:#6ab8f7
}



