
#class-viewer h1,#class-viewer .class-name-extra{display:inline-block
}
#class-viewer h1{color:#333
}
#class-viewer h2{margin-top:40px
}
#class-viewer h3{display:inline-block;margin:0;font-family:"Roboto Mono","Source Code Pro",Consolas,monospace
}
#class-viewer h3 a{color:inherit;text-decoration:none
}
#class-viewer h3 a:hover{color:#2196F3
}
#class-viewer .class-name-extra{position:relative;top:-1px;left:0.2rem;color:#777;font-family:"Roboto Condensed","Source Sans Pro",Calibri,Arial,sans-serif;font-size:1.1rem
}
#class-viewer .class-badge{position:relative;top:-4px
}
#class-viewer #class-constructor{margin-top:16px
}
#class-viewer #class-constructor pre,#class-viewer #class-constructor .param-table{margin-left:10px
}
#class-viewer .constructor-param:not(:last-child):after{content:', '
}
#class-viewer .class-item{margin:20px 2px;padding:8px
}
#class-viewer .class-item .source-button{transition:opacity 0.3s
}
@media (min-width: 61.25em){
#class-viewer .class-item .source-button{opacity:0
}
}
#class-viewer .class-item:hover .source-button{opacity:1
}
#class-viewer .class-item-details{margin-top:8px;padding:6px 0 6px 8px;border-left:2px solid #d8d8d8
}
#class-viewer code{font-family:"Roboto Mono","Source Code Pro",Consolas,monospace;font-size:0.8rem
}
#class-viewer :not(pre)>code{background:#f5f5f5;border-radius:2px
}



