
#home{text-align:center;background:#fefefe
}
#home h2{font-size:2rem;margin:0;font-weight:600;letter-spacing:-1px
}
#home p{font-size:1rem;line-height:2rem;text-align:left
}
@media (max-width: 46.24em){
#home p{margin:0 1rem
}
}
#home p>a{font-weight:bold
}
#home p.center{text-align:center
}
#home code{display:inline-block;padding:16px;border-radius:5px;background:#f5f5f5;font-family:"Roboto Mono","Source Code Pro",Consolas,monospace;color:black
}
#home #jumbotron{-ms-flex:0 0 100%;flex:0 0 100%;padding:80px 16px;background-color:#2196F3;background-image:linear-gradient(top, #2196F3, #0d8aee)
}
#home #logo{display:block;margin:16px auto;width:95%;max-width:700px;filter:drop-shadow(0 3px 4px #333)
}
#home #info{display:-ms-flexbox;display:flex;-ms-flex-flow:row wrap;flex-flow:row wrap;margin:64px auto;max-width:1120px
}
@media (max-width: 46.24em){
#home #info{margin:32px auto
}
}
#home .info-item{-ms-flex:1 1 45%;flex:1 1 45%;padding:24px
}
#home .info-item ul{display:inline-block;margin:0 auto;text-align:left
}
#home .info-item code{text-align:left;font-size:0.875rem
}
#home .full-info-item{-ms-flex:1 0 100%;flex:1 0 100%;margin:48px 0
}
#home .big-ass-btn{display:inline-block;padding:12px 10px;border-radius:4px;background:#2196F3;color:white;font-size:1.1rem
}
#home .big-ass-btn:hover{background:#51adf6
}
#home .big-ass-btn:not(:first-of-type){margin-left:8px
}
#home .big-ass-btn.discord{background-color:#7289da
}
#home .big-ass-btn.discord:hover{background:#9aabe5
}



