
#docs-sidebar{-ms-flex:0 1 auto;flex:0 1 auto;border-right:1px solid #efefef
}
@media (max-width: 46.24em){
#docs-sidebar{border-right:0
}
#docs-sidebar #docs-sidebar-content{z-index:10;position:fixed;top:0;bottom:0;left:-200px;right:0;width:0;max-height:100%;padding:0;overflow:auto;background:#f9f9f9;box-shadow:0 0 160px black;transition:300ms right, 300ms width, 300ms left
}
#docs-sidebar #docs-sidebar-content li{font-size:1.25rem !important;padding:16px !important
}
#docs-sidebar #docs-sidebar-content.open{left:0;width:100%;right:10px
}
}
#docs-sidebar #open-btn,#docs-sidebar #close-btn{z-index:1;font-size:1.1rem;cursor:pointer
}
@media (min-width: 46.25em){
#docs-sidebar #open-btn,#docs-sidebar #close-btn{display:none
}
}
#docs-sidebar #close-btn{text-align:center;padding:16px 0;color:white;background:#2196F3
}
#docs-sidebar ul{margin:0 0 16px 0;padding:0;list-style:none
}
#docs-sidebar ul li{text-transform:uppercase;font-size:1rem;font-weight:bold;color:black
}
#docs-sidebar ul ul li{padding:0;text-transform:none;font-size:0.875rem;font-weight:normal;color:#444
}
#docs-sidebar ul ul li a{display:block;padding:4px 32px 4px 4px;color:inherit;text-decoration:none;border-left:2px solid rgba(33,150,243,0)
}
#docs-sidebar ul ul li a:hover{border-color:#2196F3;color:#2196F3;background:#f9f9f9
}
#docs-sidebar .router-link-active{background:#f4f4f4
}
#docs-visibility{float:right;position:relative;right:10px;bottom:8px;padding:5px;cursor:pointer
}
@media (max-width: 46.24em){
#docs-visibility{display:none
}
}



