
.class-method p:not(.docs-see){margin:0 0 12px 0
}
.class-method h3 a:hover .method-param{color:#2196F3
}
.method-param{color:#444;font-size:1rem;transition:color 0.3s
}
.method-param.optional:before{content:'['
}
.method-param.optional:last-child:after{content:']'
}
.method-param.optional:not(:last-child):after{content:'], '
}
.method-param:not(:last-child):after{content:', '
}
.method-return{margin-top:20px;color:#6a6a6a;font-weight:bold
}
.method-return .docs-type{font-family:"Roboto Mono","Source Code Pro",Consolas,monospace
}
.method-return p{color:#2b2b2b;font-weight:normal
}
.method-throws,.method-emits,.method-examples{margin-top:16px;color:#6a6a6a;font-weight:bold
}
.method-throws pre,.method-emits pre,.method-examples pre{font-weight:normal
}



